import { Component, PropsWithChildren } from "react";
import { motion } from "framer-motion";
import styled from "styled-components";
import { Pane, PaneProps } from "evergreen-ui";

const FlexColumn = styled(Pane)`
  display: flex;
  flex-flow: column;
  height: 100%;
`;

const FlexGrow = styled.div`
  flex-grow: 1;
`;

const SafeArea = styled.div`
  height: 12px;
`;

export const FlexMotion = styled(motion.div)`
  display: flex;
  flex-flow: column;
  height: 100%;
`;

export class Flex extends Component<PaneProps> {
  static Grow = FlexGrow;

  static Motion = FlexMotion;

  static SafeArea = SafeArea;

  static Middle(props: PropsWithChildren) {
    return (
      <>
        <FlexGrow />
        {props.children}
        <FlexGrow />
      </>
    );
  }

  render() {
    return <FlexColumn {...this.props} />;
  }
}
