import { Flex } from "components/Flex";
import { Button, Heading, Image, Pane, Text, minorScale } from "evergreen-ui";
import { useState } from "react";
import { Col, Row } from "react-flexbox-grid";
import { useTranslation } from "react-i18next";
import styled from "styled-components";
import { motion } from "framer-motion";
import { wrap } from "popmotion";

export default function IntroductionPage() {
  const { t } = useTranslation();

  return (
    <>
      <Flex paddingX={minorScale(8)} paddingY={minorScale(6)}>
        <Text>{t("introduction.welcome")}</Text>

        <Flex.Middle>
          <Carousel items={Items} />
        </Flex.Middle>

        <Button appearance="action">
          {t("introduction.actions.register")}
        </Button>

        <Flex.SafeArea />
      </Flex>
    </>
  );
}

const Items = [
  {
    heading: "notifications.1",
    picture: "/assets/notifications/1.svg",
  },
  {
    heading: "notifications.2",
    picture: "/assets/notifications/2.svg",
  },
];

const variants = {
  enter: (direction: number) => {
    return {
      x: direction > 0 ? 1000 : -1000,
      opacity: 0,
    };
  },
  center: {
    zIndex: 1,
    x: 0,
    opacity: 1,
  },
  exit: (direction: number) => {
    return {
      zIndex: 0,
      x: direction < 0 ? 1000 : -1000,
      opacity: 0,
    };
  },
};
const swipeConfidenceThreshold = 10000;
const swipePower = (offset: number, velocity: number) => {
  return Math.abs(offset) * velocity;
};

function Carousel({ items }: { items: typeof Items }) {
  const { t } = useTranslation();

  const [[page, direction], setPage] = useState([0, 0]);
  const itemIndex = wrap(0, items.length, page);
  const paginate = (newDirection: number) => {
    setPage([page + newDirection, newDirection]);
  };

  return (
    <>
      <motion.div
        key={page}
        variants={variants}
        custom={direction}
        initial="enter"
        animate="center"
        exit="exit"
        drag="x"
        dragSnapToOrigin
        onDragEnd={(e, { offset, velocity }) => {
          const swipe = swipePower(offset.x, velocity.x);
          if (swipe < -swipeConfidenceThreshold) paginate(1);
          else if (swipe > swipeConfidenceThreshold) paginate(-1);
        }}
        transition={{
          x: { type: "spring", stiffness: 300, damping: 30 },
          opacity: { duration: 0.2 },
        }}
      >
        <Heading size={700} is="span">
          {t(items[itemIndex].heading)}
        </Heading>
        <Pane marginY={minorScale(12)}>
          <Row center="xs">
            <Image src={items[itemIndex].picture} />
          </Row>
        </Pane>
      </motion.div>
      <Dots
        currentItem={itemIndex}
        setItem={setPage}
        totalItems={items.length}
      />
    </>
  );
}

function Dots(props: {
  setItem(_: [number, number]): void;
  currentItem: number;
  totalItems: number;
}) {
  return (
    <Pane
      cursor="pointer"
      onClick={() => props.setItem([props.currentItem + 1, 1])}
    >
      <Row center="xs">
        {new Array(props.totalItems).fill(0).map((_, index) => (
          <Col key={index}>
            <Pane
              onClick={(e: any) => {
                e.stopPropagation();
                props.setItem([index, index > props.currentItem ? 1 : 0]);
              }}
              marginX="4px"
              borderRadius="4px"
              background={
                index === props.currentItem ? "colors.main90" : "colors.black80"
              }
            >
              <Dot
                animate={
                  index === props.currentItem ? { width: 32 } : { width: 8 }
                }
              />
            </Pane>
          </Col>
        ))}
      </Row>
    </Pane>
  );
}

const Dot = styled(motion.div)`
  height: 8px;
`;
